import { Controller, Commands } from '@ali/4ever-cangjie';

export default function insertOuput(editor: Controller, value) {
  const text = value;
  // move cursor to end of selection and insert, and focus to end of new text
  editor
    .command(Commands.focus)
    .command(Commands.moveToFocus)
    .command(Commands.insertText, text);
  return editor;
}
